.page__body {
  height: 100%;
  min-width: 304px;
  display: flex;
  flex-direction: column;
  margin: 0;
  font-family: "Montserrat", "Verdana", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  background-color: #ffffff;
  text-align: center;
  box-sizing: border-box;
}
.page__main {
  flex: 1 0 auto;
}
.page__footer {
  flex-shrink: 0;
}
.main-nav--nojs .main-nav__mobile-list-wrapper {
  display: block;
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__mobile-list-wrapper {
    width: 100%;
    order: 1;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--opened .main-nav__mobile-list-wrapper {
  display: block;
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__mobile-list-wrapper {
    position: absolute;
    top: 60px;
    width: 100%;
    z-index: 2;
  }
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 29px;
  left: 23px;
  width: 15px;
  height: 1.5px;
  background-color: #333333;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  left: 22px;
  right: 22px;
  height: 1.5px;
  background-color: #333333;
  box-shadow: 0 5.25px 0 0 #333333, 0 10.5px 0 0 #333333;
}
.main-nav__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  flex-wrap: wrap;
  min-height: 60px;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    justify-content: space-between;
    padding-right: 20px;
    padding-left: 20px;
    min-height: 80px;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    max-width: 1440px;
    margin: 0 auto;
    padding-right: 50px;
    padding-left: 40px;
  }
}
.main-nav__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
  color: #333333;
  min-height: 60px;
  margin-right: auto;
  text-decoration: none;
}
@media (max-width: 767px) {
  .main-nav__logo {
    margin-left: 20px;
  }
}
@media (min-width: 768px) {
  .main-nav__logo {
    margin-right: 0;
  }
}
@media (min-width: 1440px) {
  .main-nav__logo {
    justify-content: space-between;
  }
}
.main-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.02em;
