.price-range__scale {
  position: relative;
  width: 100%;
  height: 2px;
  margin-bottom: 33px;
  background-color: #e2e2e2;
}
.price-range__bar {
  position: absolute;
  top: -1px;
  left: 0;
  width: 92.8%;
  height: 4px;
  background-color: #9070ec;
}
.price-range__bar--disabled {
  background-color: #bdbdbd;
}
@media (min-width: 768px) {
  .price-range__bar {
    width: 202px;
  }
}
@media (min-width: 1440px) {
  .price-range__bar {
    width: 296px;
  }
}
.price-range__toggle {
  position: absolute;
  top: -10px;
  width: 24px;
  height: 24px;
  border: 2px solid #9070ec;
  border-radius: 50%;
  background-color: #ffffff;
  cursor: pointer;
  z-index: 1;
}
.price-range__toggle--min {
  left: 0;
}
.price-range__toggle--max {
  right: 0;
}
.price-range__toggle::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  top: 2px;
  left: 2px;
  border-radius: 50%;
}
.price-range__toggle:disabled {
  border: 2px solid #bdbdbd;
  cursor: default;
}
.price-range__toggle:hover:not(:disabled)::after,
.price-range__toggle:focus::after {
  background-color: rgba(120, 89, 207, 0.25);
}
.price-range__toggle:active:not(:disabled)::after {
  background-color: #7859cf;
}
.price-range__inputs {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
.price-range__inputs::after {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  left: calc(50% - 5px);
  width: 10px;
  height: 2px;
  background-color: #bdbdbd;
}
.price-range__input {
  width: 125px;
  font-family: "Montserrat", "Verdana", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  box-sizing: border-box;
  padding: 8px 10px;
  margin: 0;
  border: none;
  box-shadow: 0 0 0 2px inset #9070ec;
  border-radius: 4px;
  appearance: textfield;
}
.price-range__input:focus {
  box-shadow: 0 0 0 2px inset #593cac;
}
.price-range__input:disabled {
  box-shadow: 0 0 0 2px inset #e2e2e2;
}
.price-range__input::placeholder {
  color: #bdbdbd;
}
.price-range__input::-webkit-outer-spin-button,
.price-range__input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}
@media (min-width: 768px) {
  .price-range__input {
    width: 96px;
  }
}
@media (min-width: 1440px) {
  .price-range__input {
    width: 134px;
  }
}
.radio-field__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.radio-field__item:not(:last-child) {
  margin-bottom: 20px;
}
.radio-field__control {
  padding-left: 34px;
}
.radio-field__mark {
  width: 24px;
  height: 24px;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
  background-color: #ffffff;
}
.radio-field__mark::after {
  content: "";
  position: absolute;
  left: 2px;
  top: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
}
.radio-field__input:focus:not(:disabled) + .radio-field__mark,
.radio-field__input:hover:not(:disabled) + .radio-field__mark {
  border-color: #7859cf;
}
.radio-field__input:hover:not(:disabled) + .radio-field__mark::after,
.radio-field__input:focus:not(:disabled) + .radio-field__mark::after {
  background-color: rgba(120, 89, 207, 0.25);
}
.radio-field__input:checked + .radio-field__mark {
  border-color: #7859cf;
}
.radio-field__input:checked + .radio-field__mark::after {
  background-color: #7859cf;
}
.radio-field__input:checked:not(:enabled) + .radio-field__mark::after {
  background-color: #e2e2e2;
}
.radio-field__input:disabled + .radio-field__mark {
  border-color: #e2e2e2;
}
.control {
  position: relative;
  display: block;
  font-size: 16px;
  line-height: 24px;
  box-sizing: border-box;
}
.control__input {
  pointer-events: none;
}
.control__mark {
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  cursor: pointer;
}
.control__input:disabled + .control__mark {
  cursor: default;
}
.control__input:disabled + .control__mark + .control__label {
  color: #bdbdbd;
}
.check-field__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.check-field__item:not(:last-child) {
  margin-bottom: 20px;
}
.check-field__control {
  padding-left: 52px;
}
.check-field__mark {
  width: 42px;
  height: 24px;
  border-radius: 12px;
  background-color: #bdbdbd;
}
.check-field__mark::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  transition: all 0.2s ease-out;
}
.check-field__input:focus + .check-field__mark,
.check-field__input:hover:not(:disabled) + .check-field__mark {
  background-color: rgba(120, 89, 207, 0.25);
  box-shadow: 0 0 0 2px inset #9070ec;
}
.check-field__input:checked + .check-field__mark {
  background-color: #7859cf;
}
.check-field__input:checked + .check-field__mark::after {
  left: 20px;
}
.check-field__input:checked:not(:enabled) + .check-field__mark {
  background-color: #e2e2e2;
}
.check-field__input:checked:not(:enabled) + .check-field__mark::after {
  background-color: #bdbdbd;
}
.check-field__input:disabled + .check-field__mark {
  background-color: #e2e2e2;
}
.check-field__input:disabled + .check-field__mark::after {
  background-color: #f8f8f8;
}
